import { Component } from '@angular/core';
import {NavController, AlertController} from 'ionic-angular';
import {AngularFireDatabase, FirebaseListObservable}from "angularfire2/database";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

    tickets: FirebaseListObservable<any[]>;
    due: String = new Date().toISOString();

    constructor(public alertCtrl: AlertController, navCtrl: NavController, db: AngularFireDatabase) {

        this.tickets = db.list('/ticket');
    }

    addTicket(): void {
        let prompt = this.alertCtrl.create({
            title: "Escalated Ticket Details",
            message: "Enter Ticket Details",
            inputs: [
                {
                    name: 'room',
                    placeholder: "Complaint Location"
                },
                {
                    name: 'owner',
                    placeholder: "Ticket Owner"
                },
                {
                    name: 'status',
                    placeholder: "Ticket Status"
                },
                {
                    name: 'due',
                    placeholder: "Ticket Limit"
                },
                {
                    name: 'priority',
                    placeholder: "Ticket Priority"
                },
                {
                    name: 'created',
                    placeholder: "Date Created"
                },
                {
                    name: 'description',
                    placeholder: "Description"
                },
            ],

            buttons: [
                {
                    text: "Submit",
                    handler: data => {
                        this.tickets.push({
                            room: data.room,
                            owner: data.owner,
                            status: data.status,
                            due: data.due,
                            priority: data.priority,
                            created: data.created,
                            description: data.description

                        });
                        console.log("Submit button clicked");
                    }
                },
                {
                    text: "Cancel",
                    handler: data => {
                        console.log("Cancel button clicked");
                    }
                }

            ]


        });

        prompt.present();
    }

    editTicket(ticket): void {
        let prompt = this.alertCtrl.create({
            title: "Ticket Status",
            message: "Edit Ticket Status",
            inputs: [
                {
                    name: 'status',
                    placeholder: "open, doing, pending"
                }


            ],

            buttons: [
                {
                    text: "Submit",
                    handler: data => {
                        let newStatus: String = ticket.status;


                        if (data.status != '') {
                            newStatus = data.status;
                        }

                        this.tickets.update(ticket.$key, {
                            status: newStatus

                        });
                        console.log("Submit button clicked");
                    }
                },
                {
                    text: "Cancel",
                    handler: data => {
                        console.log("Cancel button clicked");
                    }
                }

            ]


        });

        prompt.present();
    }

    deleteTicket(ticketID): void {
        let prompt = this.alertCtrl.create({
            title: "End the task now.",
            buttons: [
                {
                    text: "Submit",
                    handler: data => {
                        this.tickets.remove(ticketID);
                        console.log("Submit button clicked");
                    }
                },
                {
                    text: "Cancel",
                    handler: data => {
                        console.log("Cancel button clicked");
                    }
                }

            ]


        });

        prompt.present();
    }

}



